body {
  background: #ecedf1;
  margin: 0;
  padding: 0;
  font-family: Libre Franklin, sans-serif;
}

canvas {
  display: block;
}

h3 > .glyphicon {
  margin-right: .5em;
  font-size: 80%;
}

h3 {
  font-weight: 700;
}

.main {
  padding-left: 340px;
}

.title {
  color: #fff;
  font-size: 22px;
  font-weight: 700;
}

.controls h4 {
  color: #e91e63;
  border-bottom: 1px solid #3d495a;
  padding: 5px 0;
  font-size: 16px;
  font-weight: 700;
}

.renderer {
  height: 200px;
  border-radius: 4px;
  margin-bottom: 10px;
}

.renderer canvas {
  width: 100%;
  height: 100%;
}

.code-display {
  border: 0;
  font-size: 12px;
}

.code-display code {
  min-height: 200px;
}

.controls {
  height: 100%;
  -webkit-overflow-scrolling: touch;
  color: #cfd4db;
  width: 340px;
  user-select: none;
  box-sizing: border-box;
  background: #272d37;
  font-size: 12px;
  position: fixed;
  top: 0;
  left: 0;
  overflow: auto;
}

.hljs {
  background-color: #282e38;
}

.controls::-webkit-scrollbar {
  width: 8px;
  background-color: #0000;
}

.controls::-webkit-scrollbar-thumb {
  background-color: #647086;
  border-radius: 4px;
}

.config {
  margin-bottom: 4px;
}

.config label {
  padding-top: 4px;
}

.config .glyphicon {
  margin-right: 5px;
}

.well {
  background-color: #d8d9e0;
}

.controls h4 {
  cursor: pointer;
}

.controls .collapsed:before {
  content: "▸";
  width: 20px;
  display: inline-block;
}

.controls .expanded:before {
  content: "▾";
  width: 20px;
  display: inline-block;
}

.controls .row {
  min-height: 36px;
}

.controls label {
  margin-top: 5px;
  font-weight: normal;
}

.controls input, .controls textarea, .controls select {
  color: #fff;
  background-color: #14171d;
  border-color: #3d495a;
}

.gradient {
  margin-bottom: 8px;
}

.gradient .color-group {
  margin-bottom: 5px;
}

.gradient .color-group .btn {
  padding-left: 4px;
  padding-right: 4px;
}

.gradient .color-group .glyphicon {
  font-size: 10px;
}

.check {
  margin-top: 8px !important;
  margin-right: 5px !important;
}

.color {
  padding: 0 !important;
}

.color::-webkit-color-swatch-wrapper {
  padding: 0;
}

.color::-webkit-color-swatch {
  border: 1px solid #0006;
}

.btn-file {
  position: relative;
  overflow: hidden;
}

.btn-file input[type="file"] {
  min-width: 100%;
  min-height: 100%;
  text-align: right;
  opacity: 0;
  cursor: inherit;
  background: #fff;
  outline: none;
  font-size: 100px;
  display: block;
  position: absolute;
  top: 0;
  right: 0;
}

.btn-group-full {
  display: flex;
}

.btn-group-full .btn {
  width: 100%;
}

.font-loader {
  min-height: 30px !important;
}

@media (max-width: 760px) {
  .controls {
    width: 260px;
  }

  .main {
    padding-left: 260px;
  }

  .title {
    height: 30px;
    font-size: 160%;
  }
}

@media (max-width: 480px) {
  .controls {
    width: 100%;
    height: 50%;
    position: absolute;
  }

  .main {
    height: 50%;
    width: 100%;
    -webkit-overflow-scrolling: touch;
    border-top: 2px dotted #999;
    padding: 0;
    position: fixed;
    top: 50%;
    overflow: auto;
  }
}

